<template>
    <div style="top:100px;width:300px">
        <span></span>
        <el-form inline="">
            <el-form-item>
                <el-button type="primary" @click="handleTest"></el-button>
            </el-form-item>
            <el-form-item>
                {{this.result}}
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="handleTest2">1</el-button>
            </el-form-item>
            <el-form-item>
                {{this.crosResult}}
            </el-form-item>
             <el-form-item>
                <el-button type="primary" @click="handleTest3">2</el-button>
            </el-form-item>
            <el-form-item>
                {{this.crosResult2}}
            </el-form-item>
        </el-form>
        <!-- <a href="./static/img/1.jpg"></a>--> 
       <!--<img :src="this.imgUrl" />--> 
    </div>
</template>
<script>
import TEST from '@/api/test'
import axios from 'axios'
export default {
  data() {
    return {
      result:'',
      result2: '',
      result3: '',
      imgUrl: 'http://localhost:8090/webapp2/static/img/1.jpg'
    }
  },
  computed:{
            crosResult(){
                return this.result2;
             },
             crosResult2(){
                return this.result3;
             }
        },
  methods: {
    handleTest() {
      var params = {}
      TEST.test(params).then(r => {
        //,
        if (r.data.code === '200') {
          this.result = r.data.data
          this.$notify({
            title: 'success',
            message: '',
            type: 'success',
            position: 'bottom-right'
          })
        } else {
          this.$notify.error({
            title: 'error',
            message: ':' + r.data.msg,
            type: 'error',
            position: 'bottom-right'
          })
        }
      })
    },
    handleTest2() {
      const self=this;
      axios
        .get('http://localhost:8080/webapp1/test', {
          params: {}
        })
        .then(function(res) {
         // console.log(JSON.stringify(res))
         self.result2=res.data.data
         //alert(''+self.result2)
          self.$notify({
            title: 'success',
            message: '!',
            type: 'success',
            position: 'bottom-right'
          })
        })
        .catch(function(res) {
          console.log(JSON.stringify(res.data))
         //alert( ':' + JSON.stringify(res))
           self.$notify.error({
            title: 'error',
            message: ',error:' + JSON.stringify(res),
            type: 'error',
            position: 'bottom-right'
          })
        })
    },
     handleTest3() {
      const self=this;
      axios
        .get('http://localhost:8090/webapp2/test', {
          params: {}
        })
        .then(function(res) {
         // console.log(JSON.stringify(res))
         self.result3=res.data.data
         //alert(''+self.result2)
          self.$notify({
            title: 'success',
            message: '!',
            type: 'success',
            position: 'bottom-right'
          })
        })
        .catch(function(res) {
          console.log(JSON.stringify(res.data))
         //alert( ':' + JSON.stringify(res))
           self.$notify.error({
            title: 'error',
            message: ',error:' + JSON.stringify(res),
            type: 'error',
            position: 'bottom-right'
          })
        })
    }
  }
}
</script>



